import { tableData } from '../spectaculars/json'
import { hasClass, removeClass, addClass, siblings } from '../../frame_template/js/utils';
export default {
  data() {
    return {
      stageList:[],
      stage:'',
      heshiList: [{
        value: '',
        label: '全部'
      },{
        value: '1',
        label: '已核实'
      }, {
        value: '2',
        label: '未核实'
      }],
      heshi:'',
      
      value: '',
      radio: 0,

			tableData: [],
			townId:'',
			townList:[],

			//状态码
			CITY: 1,
			DISTRICT: 2,
			SCHOOL: 3,
			showSchool: true,
			showSchoolInfo: true,
			ifCheck:sessionStorage.getItem("ifCheck"),
			lastStatus:sessionStorage.getItem("lastStatus"),
			nextStatus:sessionStorage.getItem("nextStatus"),
			status:sessionStorage.getItem("status"),
      dialogVisible: false,
      dialogImageUrl: '',
			month:'',
			projectId: this.$route.params.projectId,
			projectName: this.$route.params.projectName,
			checked:false,
			ifcheck:'0'
    }
  },

	computed: {
		tMenu() {
			return this.tableData[0].scheduleList;
		}
	},

  mounted() {
		this.$nextTick(function() {
//			this.tableData = tableData;
//			this.queryInit();
				this.queryStage();
				this.queryTown();
				this.queryInit();
		})

  },

  methods: {
  	changeCheck(){
			if(this.checked){
				this.ifcheck = 'kanban'
			}else{
				this.ifcheck = '0'
			}
			this.queryInit();
		},
  	
  	queryInit(){
				var temp = this;
				this.$post('/web/kanbanList', {"projectId":temp.projectId,"month":temp.month,"status":temp.lastStatus,"level":"1",stage:temp.stage,"heshi":temp.heshi,"town_id":temp.townId,"checked" : temp.ifcheck}).then(function(m) {
					temp.tableData=m.content.list;
					if(temp.month == ''){
						temp.month = m.content.month;
					}
				})
			},
			
    //放大显示上传的图片
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url || file;
      this.dialogVisible = true;
    },
		check(status,id, checkType, lastStatus) {
				var temp = this;
				temp.$post('/web/checkStatus', {
					"checkType": checkType,
					"status": status,
					"lastStatus": lastStatus,
					"projectId": temp.projectId,
					"id": id,
					"month": temp.month,
				}).then(function(m) {
					temp.$message({
						type: 'success',
						message: '审核成功!'
					});	
					temp.queryInit();
				}).catch((err) => {
				})
			},
			
			queryStage(){
				var temp = this;
				this.$post('/web/queryStage', {"projectId":temp.projectId}).then(function(m) {
					temp.stageList=m.content.list;
					temp.stageList.unshift({id: '',name:'全部'});
				})
			},
			
			queryTown(){
				var temp = this;
				this.$post('/web/queryTownByProjectId', {"projectId":temp.projectId}).then(function(m) {
					temp.townList=m.content.list;
					temp.townList.unshift({id: '',name:'全部'});
				})
			},

		//行点击展开事件
		openSchoolInfo(e, item) {
			const target = e.target;
			if( item.level == this.DISTRICT ) {
				this.getChildren(`.school_${item.id}`);
			}else if( item.level == this.SCHOOL ) {
				this.getChildren(`.school-info_${item.id}`);
			}
		},

		getChildren(childClassName) {
			const wrap = document.querySelector("#spectaculars-table");
			let child = wrap.querySelectorAll(childClassName);
			child.forEach(i => {
				hasClass(i, 'hide') ? removeClass(i, 'hide') : addClass(i, 'hide')
			})
		},

		//学校重点说明合并单元格
		rowMerge(item) {
			if( !!item.invertedList ) {
				return 4
			}else {
				return 1
			}
		},

		//设置行class
		setRowClass(item) {
			if( item.level === this.DISTRICT ) {
				return 'district'
			}else if( item.level === this.SCHOOL ) {
				return `school_${item.id}`
			}
		}

  }
}



